.SearchBar_container__2mcb9 {
    --border-radius: 12px;
    display: -webkit-flex;
    display: flex;
    max-width: 30rem;
    margin: 0 auto;
    border-radius: var(--border-radius);
}

form input {
    font-family: 'Rubik';
    font-size: 1.125rem;
    color: hsl(0, 0%, 17%);
    padding: 1rem 1.4rem;
    -webkit-flex-grow: 1;
            flex-grow: 1;
    min-width: 0;
    border: none;
    border-radius: var(--border-radius) 0 0 var(--border-radius);
    outline: none;
}

form button {
    background-color: black;
    width: 3.4rem;
    -webkit-flex-shrink: 0;
            flex-shrink: 0;
    border: none;
    border-radius: 0 var(--border-radius) var(--border-radius) 0;
}
dl {
    text-align: center;
    width: 100%;
    margin: 0;
    box-sizing: border-box;
}

.dataItem {
    padding: 0.6rem 0;
}

dt {
    font-size: 0.625rem;
    font-weight: bold;
    color: hsl(0, 0%, 59%);
    text-transform: uppercase;
    letter-spacing: 0.125rem;
}

dd {
    font-size: 1.2rem;
    font-weight: 500;
    color: hsl(0, 0%, 17%);
    margin: 0;
    margin-top: 0.4rem;
}

hr {
    display: none;
}

.collapsible {
    pointer-events: none;
    max-width: 30rem;
    margin: 0 auto;
}

.open.collapsible {
    pointer-events: auto;
}

.collapsible::before,
.collapsible::after {
    content: "";
    display: block;
    width: 100%;
    height: 0.6rem;
    background: white;
}

.collapsible::before {
    border-radius: 0.5rem 0.5rem 0 0;
}

.collapsible::after {
    margin-top: -1px;
    border-radius: 0 0 0.5rem 0.5rem;
    -webkit-transform: translateY(var(--translate));
            transform: translateY(var(--translate));
    transition: -webkit-transform 0.3s ease;
    transition: transform 0.3s ease;
    transition: transform 0.3s ease, -webkit-transform 0.3s ease;
}

.collapsible.open::after {
    -webkit-transform: translateY(0);
            transform: translateY(0);
}

.collapsible-bg {
    position: relative;
}

.collapsible-bg::before {
    content: "";
    display: block;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: white;
    -webkit-transform-origin: top;
            transform-origin: top;
    -webkit-transform: scaleY(var(--scale));
            transform: scaleY(var(--scale));
    transition: -webkit-transform 0.3s ease;
    transition: transform 0.3s ease;
    transition: transform 0.3s ease, -webkit-transform 0.3s ease;
    z-index: -1000;
}

.open .collapsible-bg::before {
    -webkit-transform: scaleY(1);
            transform: scaleY(1);
    transition: -webkit-transform 0.3s ease;
    transition: transform 0.3s ease;
    transition: transform 0.3s ease, -webkit-transform 0.3s ease;
}

.dataItem {
    color: white;
    opacity: 0;
    -webkit-transform: translateY(var(--translate));
            transform: translateY(var(--translate));
    transition: opacity 0.2s ease, -webkit-transform 0.3s ease;
    transition: transform 0.3s ease, opacity 0.2s ease;
    transition: transform 0.3s ease, opacity 0.2s ease, -webkit-transform 0.3s ease;
}

.dataItem[data-pivot] {
    opacity: 1;
}

.open .dataItem {
    opacity: 1;
    -webkit-transform: translateY(0);
            transform: translateY(0);
    transition: opacity 0.2s ease 0.1s, -webkit-transform 0.3s ease;
    transition: transform 0.3s ease, opacity 0.2s ease 0.1s;
    transition: transform 0.3s ease, opacity 0.2s ease 0.1s, -webkit-transform 0.3s ease;
    pointer-events: none;
}

.collapsible-btn {
    padding: 0.5rem 0;
    -webkit-transform: translateY(var(--translate));
            transform: translateY(var(--translate));
    transition: -webkit-transform 0.3s ease;
    transition: transform 0.3s ease;
    transition: transform 0.3s ease, -webkit-transform 0.3s ease;
    pointer-events: auto;
}

.open .collapsible-btn {
    -webkit-transform: translateY(0);
            transform: translateY(0);
}

.collapsible-btn img {
    -webkit-transform: rotate(90deg);
            transform: rotate(90deg);
}

.open .collapsible-btn img {
    -webkit-transform: rotate(-90deg);
            transform: rotate(-90deg);
}

@media screen and (min-width: 900px) {
    .collapsible {
        max-width: 52rem;
    }

    .collapsible::before,
    .collapsible::after,
    .collapsible-bg::before,
    .dataItem {
        -webkit-transform: none;
                transform: none;
        transition: none;
    }

    dl {
        display: -webkit-flex;
        display: flex;
        padding: 1.4rem 0;
    }
    
    .dataItem {
        margin: 0;
        padding: 0 1.8rem;
        text-align: left;
        box-sizing: border-box;
        -webkit-flex: 1 0;
                flex: 1 0;
        opacity: 1;
    }

    .dataItem[data-pivot] {
        pointer-events: none;
    }

    .collapsible-btn {
        display: none;
    }

    hr + .dataItem {
        margin: 0;
    }

    hr {
        display: block;
        border: 0;
        border-left: 1px solid hsl(0deg 0% 82%);
        margin: 0.3rem 0;
    }
}

@media screen and (min-width: 1200px) {
    .collapsible {
        max-width: 62rem;
    }

    dl {
        padding: 1.8rem 0;
    }

    .dataItem {
        padding: 0 2.125rem;
    }

    dt {
        font-size: 0.75rem;
    }

    dd {
        font-size: 1.5rem;
        margin-top: 0.7rem;
    }
}
.snackbar {
    position: absolute;
    margin-left: 50%;
    -webkit-transform: translate(-50%, 0);
            transform: translate(-50%, 0);
    bottom: 2rem;
    border-radius: 6px;
    text-align: center;
    color: white;
    z-index: 1000;
}

.snackbar .container {
    padding: 1rem 2rem;
}

@-webkit-keyframes slide-in {
    from {
        opacity: 0;
        -webkit-transform: translate(-50%, 20%);
                transform: translate(-50%, 20%);
    }

    to {
        opacity: 1;
        -webkit-transform: translate(-50%, 0);
                transform: translate(-50%, 0);
    }
}

@keyframes slide-in {
    from {
        opacity: 0;
        -webkit-transform: translate(-50%, 20%);
                transform: translate(-50%, 20%);
    }

    to {
        opacity: 1;
        -webkit-transform: translate(-50%, 0);
                transform: translate(-50%, 0);
    }
}

@-webkit-keyframes slide-out {
    from {
        opacity: 1;
        -webkit-transform: translate(-50%, 0);
                transform: translate(-50%, 0);
    }

    to {
        opacity: 0;
        -webkit-transform: translate(-50%, -20%);
                transform: translate(-50%, -20%);
    }
}

@keyframes slide-out {
    from {
        opacity: 1;
        -webkit-transform: translate(-50%, 0);
                transform: translate(-50%, 0);
    }

    to {
        opacity: 0;
        -webkit-transform: translate(-50%, -20%);
                transform: translate(-50%, -20%);
    }
}
